const STORAGE_KEY = 'theme'

export default {
    namespaced: true,
    state: () => ({
        mode: localStorage.getItem(STORAGE_KEY) || 'light',
    }),
    mutations: {
        SET_THEME(state, name) {
            state.mode = name
        },
    },
    actions: {
        setMode({ commit }, name) {
            commit('SET_THEME', name)
            localStorage.setItem(STORAGE_KEY, name)
        },
        initTheme({ commit }) {
            const saved = localStorage.getItem(STORAGE_KEY)
            if (saved) commit('SET_THEME', saved)
        },
    },
    getters: {
        currentTheme: (state) => state.mode,
    },
}