const defaultTitle = "KIM HYEOK JUN : "
export default [
    {
        path: '/',
        component: () => import('@/layouts/DefaultLayout.vue'),
        children: [
            {
                path: 'dashboard',
                name: 'dashboard',
                component: () => import('@/views/dashboard/DashboardView.vue'),
                meta: {
                    title: defaultTitle + 'DASH BOARD'
                }
            },
            {
                path: 'project',
                name: 'projectList',
                component: () => import('@/views/project/ProjectListView.vue'),
                meta: {
                    title: defaultTitle + 'PROJECT LIST'
                },
            },
            {
                path: '/project/details',
                name: 'projectDetails',
                component: () => import('@/views/project/ProjectDetailsView.vue'),
                meta: {
                    title: defaultTitle + 'PROJECT DETAIL'
                }
            }
            ,
            {
                path: '/profile',
                name: 'profile',
                component: () => import('@/views/profile/ProfileView.vue'),
                meta: {
                    title: defaultTitle + 'PROJECT PROFILE'
                }
            }
        ],
        /* redirect: '/dashboard' */
    },
]