import { createStore } from 'vuex'
import createPersistedState from "vuex-persistedstate"
import countStore from './modules/count.store'
import theme from './modules/theme.store'

const store = createStore({
  plugins: [
    createPersistedState({
      key: 'visit_stats',     // 원하는 키
      paths: ['countStore'],  // ✅ 이 모듈만 저장/복원
      // storage: window.localStorage, // 기본값이 localStorage라 생략 가능
    }),
  ],
  modules: {
    countStore,
    theme,
    //...colorStore
  }
})

export default store