const dayKeyKST = (d = new Date()) =>
  d.toLocaleDateString('en-CA', { timeZone: 'Asia/Seoul' })

const monthKeyKST = (d = new Date()) => {
  // 'YYYY-MM'
  return new Intl.DateTimeFormat('en-CA', {
    timeZone: 'Asia/Seoul',
    year: 'numeric',
    month: '2-digit',
  }).format(d)
}

const lastNDaysKeys = (n) => {
  const res = []
  for (let i = n - 1; i >= 0; i--) {
    const d = new Date(Date.now() - i * 86400000)
    res.push(dayKeyKST(d))
  }
  return res
}

const lastNMonthsKeys = (n) => {
    const res = []
    const base = new Date()
    for (let i = n - 1; i >= 0; i--) {
        const d = new Date(base)
        d.setDate(1)                // 월 계산 안정화
        d.setMonth(d.getMonth() - i)
        res.push(monthKeyKST(d))
    }
    return res
}

const countStore = {
    namespaced: true,
    state: {
        date: dayKeyKST(),
        today: 0,
        total: 0,

        daily:   {},
        monthly: {},

        retentionDays: 400,
        retentionMonths: 36,
    },
    getters: {
        weekKeys: () => lastNDaysKeys(7),
        weekCategories: (s, g) => g.weekKeys.map(k => k.slice(5)), // 'MM-DD'
        weekData: (s, g) => g.weekKeys.map(k => s.daily[k] || 0),

        // --- 연간(최근 7개월) ---
        monthKeys7: () => lastNMonthsKeys(7),
        monthCategories7: (s, g) => g.monthKeys7.map(k => {
        // 보기 좋은 라벨: 'MM월' (연도 넘어가면 'YY.MM')
            //const yyyy = k.slice(0, 4)
            const mm = k.slice(5, 7)
            return mm + '월'
        }),
        monthData7: (s, g) => g.monthKeys7.map(k => s.monthly[k] || 0),

        today: (s) => s.today,
        total: (s) => s.total,
    },
    mutations: {
        ROLLOVER(state) {             // 날짜 바뀌면 today 초기화
            const now = dayKeyKST()
            if (state.date !== now) {
                state.date = now
                state.today = 0
            }
        },
        INCREMENT(state) {
            const dKey = dayKeyKST()
            const mKey = monthKeyKST()

            state.today += 1
            state.total += 1

            const nextDaily = { ...state.daily }
            nextDaily[dKey] = (nextDaily[dKey] || 0) + 1
            state.daily = nextDaily

            const nextMonthly = { ...state.monthly }
            nextMonthly[mKey] = (nextMonthly[mKey] || 0) + 1
            state.monthly = nextMonthly
        },
        PRUNE(state) {
            const keepDays = new Set(lastNDaysKeys(state.retentionDays))
            for (const k of Object.keys(state.daily)) {
                if (!keepDays.has(k)) delete state.daily[k]
            }

            const keepMonths = new Set(lastNMonthsKeys(state.retentionMonths))
            for (const k of Object.keys(state.monthly)) {
                if (!keepMonths.has(k)) delete state.monthly[k]
            }
        },
        RESET(state) {
            state.date = dayKeyKST()
            state.today = 0
            state.total = 0
            state.daily = {}
            state.monthly = {}
        },
    },
    actions: {
        hit({ commit }, { oncePerSession = false } = {}) {
            if (oncePerSession) {
                const key = `visited_${dayKeyKST()}`
                if (sessionStorage.getItem(key)) return
                sessionStorage.setItem(key, '1')
            }
            commit('ROLLOVER')
            commit('INCREMENT')
            commit('PRUNE')
        },
    },
}

export default countStore;