import { createApp } from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import router from './router'
import store from './store'
import ApexCharts from 'apexcharts'

loadFonts()

router.beforeEach((to, from, next) => {
  const defaultTitle = 'KIM HYEOK JUN'
  document.title = to.meta.title || defaultTitle
  next()
})

store.commit('countStore/ROLLOVER')

store.dispatch('countStore/hit', { oncePerSession: true })

store.dispatch('theme/initTheme')

const media = window.matchMedia('(prefers-color-scheme: dark)')
function applyThemeFromStore() {
  const mode = store.state.theme.mode
  if (mode === 'system') {
    vuetify.theme.global.name.value = media.matches ? 'dark' : 'light'
  } else {
    vuetify.theme.global.name.value = mode
  }
}
applyThemeFromStore()

store.subscribe((mutation) => {
  if (mutation.type === 'theme/SET_THEME') {
    applyThemeFromStore()
  }
})

const onMediaChange = () => {
  if (store.state.theme.mode === 'system') {
    applyThemeFromStore()
  }
}

if (media.addEventListener) media.addEventListener('change', onMediaChange)
  else media.addListener(onMediaChange)


const app = createApp(App)

app.config.globalProperties.$chart = ApexCharts
app.use(router).use(vuetify).use(store).mount('#app')
